import React, { useEffect, useState, useRef } from "react";

import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys, hasWritePrivilege, parseDateMMDDYYYY } from "../../../stores/Utils";

import "./dashboard.css"
import { OpenModal } from "../../../components/DialogHRM";
import { getall_invoice, sortInvoices, delete_invoice } from "../services/server_fetching";
import { SvgIcon_Cmpt } from "../../../components";
import DeleteAlert from "./deleteAlert";
import NoPrivilege from "./NoPrivilege";
import { ViewInvoice_Cmpt } from "..";


const Svg = SvgIcon_Cmpt();


const ViewInvoice = ViewInvoice_Cmpt();



const InvoiceItem  = ({ data, elmId, handleRefreshAll }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			// let frm2 = {"firstName":"Belisario","lastName":"Gonzalez Arevalo","genre":"Male","dateOfBirth":"06/02/1976","placeOfBirth":{"city":"Holguin","state":"Holguin"},"currentAddress":{"No":"4307","street":"Estate Dr","city":"Louisville","state":"Kentucky","zipcode":"40216"},"dob":202536000000}
			//updStore(frmNm, frm2);
			set_obs(gen16CodeId())
		}
	});

	
	const updObs =  () => {
		set_obs(gen16CodeId())
	}



	const refreshAccs =  () => {
		getall_invoice(updObs)
	}

	const update = async () => {
		if(hasWritePrivilege()){
			updStore("upd_invoice", data);
			updStore("upd_invoice_bck", JSON.parse(JSON.stringify(data)));
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "80vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true };
			//modaldata["content"] = <UpdInvoice confirm={refreshAccs} />;
			OpenModal(modaldata);
		}
	}

	
	


	const openSubMayor =  (sba) => {
		

		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] =  <ViewInvoice confirm={refreshAccs} item={data}/>
		OpenModal(modaldata);
	}


	const deleteComprobante =  () => {
		delete_invoice(data?.invoiceId, handleRefreshAll);
	}
	

	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(hasWritePrivilege()){
			if( Date.now() > lastTimeCallModal){
				updStore("lastTimeCallModal", Date.now()+320);
				let modaldata = {};
				modaldata["zIndex"] = 450;
				modaldata["height"] = "40vh";
				// data['observeResize']=true;
				modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
				modaldata["content"] = <DeleteAlert confirm={deleteComprobante} item={data} />
				OpenModal(modaldata);
			}
		}else{
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <NoPrivilege />
			OpenModal(modaldata);
		}
	}

	

	let subAcc = sortInvoices( getStore("sub_invoice_group")?.[data.invoiceId],"subInvoice");
	
	

	return (
		<div className="item_acc">
			<div className="_dsplFlx item_box invoice_header">
				<div className="name" >
					{parseDateMMDDYYYY(data?.date)} 
				</div>
				<div className="code" style={{marginLeft:15}}>
					{data?. document}
				</div>
				<div className="description" style={{marginLeft:15}}>
					{data?. description}
				</div>
				<div className="flexSpace"/>
				
				<div className="flexSpace"/>
				<div className=" addSbAcc purple" onClick={()=>openSubMayor(null)}>
					See Details
				</div>
				<div className="name hideOnHover">
					${(data?. amount*1).toFixed(2)}
				</div>
				{hasWritePrivilege()?
				<div className="icon" style={{marginLeft: 19,zIndex:10}} onClick={OpenDeleteAlert}>
					<Svg name={"outline_delete"} color={"#c62828"}/>
				</div>
				:null}
			</div>
			
		</div>
	)
}
export default InvoiceItem;